<template lang="">
    <div id="container">
        <h2>IU list</h2>
        <div class="in_wrap">
            <div class="content" v-for="(c,i) in IUs" :key="i">
                <img :src="c.image" :alt="c.id" @click="modalOpen(i)"/>
                <h3 @click="modalOpen(i)">{{c.title}}</h3>
                <ModalVue v-if="modal" :modal="modal" :coffee="IUs" :crt="crt" @closeModal="modalClose">
                </ModalVue>
            </div>
        </div>
    </div>
</template>
<script>
import IU from '../assets/IU.js'
import ModalVue from './ModalVue'

export default {
    data(){
        return{
            IUs : IU,
            modal:false,
            crt:0
        }
    },
    methods: {
        modalOpen(i){
            this.modal = true;
            this.crt = i;
        },
        modalClose(){
            this.modal=false;
            
        }
    },
    components:{
        ModalVue
    }
}
</script>
<style scoped>
    #container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: auto;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
</style>