<template lang="">
    <div id="container">
        <h2>coffee list</h2>
        <div class="in_wrap">
            <div class="content" v-for="(c,i) in coffees" :key="i">
                <img :src="c.image" :alt="c.id" @click="modalOpen(i)"/>
                <h3 @click="modalOpen(i)">{{c.title}}</h3>
                <ModalVue v-if="modal" :modal="modal" :coffee="coffees" :crt="crt" @closeModal="modalClose">
                </ModalVue>
            </div>
        </div>
        <button class="more" @click="more">더보기</button>
    </div>
</template>
<script>
import axios from 'axios';
import coffee from '../assets/coffee.js'
import ModalVue from './ModalVue'

export default {
    data(){
        return{
            coffees:coffee,
            modal:false,
            crt:0,
            moreList:2,
        }
    },
    methods: {
        more(){
            axios.get(`https://joonsol.github.io/data/more${this.moreList}.json`)
                 .then( result => {
                    this.coffees.push(result.data);
                    if(this.moreList == 0){
                            this.moreList = 1;
                    }else{
                        this.moreList = 0;
                    }
                })
        },
        modalOpen(i){
            this.modal = true;
            this.crt = i;
        },
        modalClose(){
            this.modal=false;
            
        }
    },
    components:{
        ModalVue
    }
}
</script>
<style scoped>
#container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: 200px;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }

 .more {
    border: none;
    color: rgb(77, 77, 77);
    border-radius: 2rem;
    padding: 0 1rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 3rem;
    cursor: pointer;
  }
</style>