import { createWebHistory, createRouter } from "vue-router";
import Product from './components/ProductVue.vue'
import Service from './components/ServiceVue.vue'
import Company from './components/CompanyVue.vue'
import Iu from './components/IuVue.vue'
import LastWord from './components/LastWordVue.vue'

const routes = [
    {
        path: '/', 
        component: Product
    },
    {
        path: '/service',
        component: Service
    },
    {
        path: '/Company',
        component: Company
    },
    {
        path: '/Iu',
        component: Iu
    },
    {
        path: '/LastWord',
        component: LastWord
    }
]
const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router;     