<template lang="" >
    <h2>{{lastWord}}</h2>
    <div class="inputBox shadow">
        <input type="text" v-model="newWord" placeholder="끝말잇기 게임입니다." v-on:keyup.enter="addTodo">
        <button class="svc_btn" v-on:click="addTodo">입력</button>
    </div>

    <modal :show="showModal" v-if="showModal" @close="showModal = false">
        <!-- <h3 slot="header">경고</h3>
        <span slot="footer" @click="showModal = false"> -->
        <h3>안내</h3>
        <span @click="showModal = false">
            끝말을 입력하세요
            <i class="closeModalBtn fas fa-times" aria-hidden="true"></i>
        </span>
    </modal>

    <section>
        <transition-group name="list" tag="ul">
          <li v-for="(todoItem, index) in todoItems" v-bind:key="todoItem" class="shadow">
            <i class="checkBtn fas fa-check" aria-hidden="true"></i>
            {{todoItem}}
            <button class="removeBtn" @click="removeTodo(todoItem, index)">삭제</button>
          </li>
        </transition-group>
      </section>
<br/>
      <div class="clearAllContainer">
        <span class="clearAllBtn" @click="clearAll">Clear All</span>
      </div>
</template>
<script>
import word from '../assets/word.js'
import vue_Modal from './common/vue_Modal.vue'

export default {
    data() {
        return {
            todoItems:[],
            words : word,
            lastWord:'',
            newWord:'',
            result:'',
            showModal: false
        }
    },
    created() {
        this.lastWord = this.words[0].word;

        if(0 < localStorage.length){
            for(var i=0; i < localStorage.length; i++){
                this.lastWord = localStorage.key(localStorage.length-1);
                this.todoItems.push(localStorage.key(i));
            }
        }
    },
    methods: {
        addTodo(){
            if(this.newWord !=="" && 
                (this.newWord.substring(0,1) === this.lastWord.substr(this.lastWord.length-1))){
                var value = this.newWord && this.newWord.trim();
                this.lastWord = value;
                localStorage.setItem(value,value);
                this.todoItems.push(value);
            }else{
                this.showModal = !this.showModal;
            }
            this.cleatInput();
        },
        cleatInput(){
            this.newWord = '';
        },
        removeTodo(todoItem, index){
            localStorage.removeItem(todoItem);
            this.todoItems.splice(index,1);
        },
        clearAll(){
            localStorage.clear();
            this.todoItems = [];
            this.lastWord = this.words[0].word;
        },
    },
    components:{
        Modal: vue_Modal
    }
}
</script>
<style scoped>
input:focus {
  outline: none;
}
.inputBox {
  background: white;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
}
.inputBox input {
  border-style: none;
  font-size: 0.9rem;
}
.addContainer {
  float: right;
  background: linear-gradient(to right, #6478FB, #8763FB);
  display: inline-block;
  width: 3rem;
  border-radius: 0 5px 5px 0;
}
.addBtn {
  color: white;
  vertical-align: middle;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.svc_btn {
    border: none;
    padding: .5rem 2rem;
    border-radius: 3rem;
    cursor: pointer;
  }

  .clearAllContainer {
    width: 8.5rem;
    height: 50px;
    line-height: 50px;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
  }
  .clearAllBtn {
    color: #e20303;
    display: block;
  }
</style>
